import React, { useEffect, useState, useRef } from "react";
import { useNavigate, useParams } from "react-router-dom";
import {
  Autocomplete,
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  Typography,
  Alert,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Tabs,
  Tab,
  Radio,
  RadioGroup,
  FormControlLabel,
  Paper,
  InputAdornment,
} from "@mui/material";
import LockIcon from "@mui/icons-material/Lock";
import {
  getBBPSBill,
  getBBPSOperator,
  getMobileRechargeCircle,
  getMobileRechargePlan,
  payBBPSBill,
} from "../../api/login/Login";
import Loader from "../../common/loader/Loader";
import withLocation from "../../utils/withLocation";
import toastMessage from "../../utils/toastMessage";
import { ToastContainer } from "react-toastify";
import { genrateUrl } from "../../utils/genrateUrl";
import CloseIcon from "@mui/icons-material/Close";
import IconButton from "@mui/material/IconButton";
const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    fullWidth
    sx={{
      "& .MuiInputLabel-root": {
        color: "var(--bs-primary2)",
      },
      "& .MuiOutlinedInput-root": {
        height: 42,
        "& input, & textarea": {
          color: "var(--bs-primary2) !important",
        },
        "& fieldset": {
          borderColor: "#000000",
        },
        "&:hover fieldset": {
          borderColor: "#000000",
        },
        "&.Mui-focused fieldset": {
          borderColor: "#000000",
        },
      },
    }}
    InputLabelProps={{
      shrink: true,
    }}
    {...props}
  />
);

const CustomBox = ({ children }) => (
  <Box
    sx={{
      width: "100%",
      mx: "auto",
      my: 1,
      px: 3,
      py: 4,
      pb: 6,
      backgroundColor: "var(--bs-primary3)",
      color: "var(--bs-primary2)",
    }}
  >
    {children}
  </Box>
);

const BBPSServices = ({ walletCall }) => {
  const navigate = useNavigate();
  const { serviceId } = useParams();
  const [loading, setLoading] = useState(false);
  const [operatorList, setOperatorList] = useState([]);
  const [error, setError] = useState("");
  const [selectedOperator, setSelectedOperator] = useState(null);
  const [formValues, setFormValues] = useState({});
  const [formErrors, setFormErrors] = useState({});
  const [billDetails, setBillDetails] = useState(null);
  const [billAmount, setBillAmount] = useState("");
  const [billFetched, setBillFetched] = useState(false);
  const [pin, setPin] = useState();
  const [refId, setRefId] = useState("");
  const [openTipModel, setOpenTipModel] = useState(false);
  const [serviceName, setServicename] = useState("");
  const isFetchingRef = useRef(false);

  // Mobile Recharge Special State
  const [isMobileRecharge, setIsMobileRecharge] = useState(false);
  const [isDth, setIsDth] = useState(false);
  const [fetchRequired, setFetchRequried] = useState(false);
  const [circle, setCircle] = useState([]);
  const [selectedCircle, setSelectedCircle] = useState(null);
  const [planDetails, setplanDetails] = useState(null);
  const [planPopupOpen, setPlanPopupOpen] = useState(false);

  useEffect(() => {
    if (!serviceId) {
      setError("Invalid service ID");
      return;
    }

    const fetchOperators = async () => {
      setLoading(true);
      setIsMobileRecharge(false);
      setIsDth(false);
      try {
        const res = await getBBPSOperator(serviceId);
        if (res?.data?.error) {
          setError("Failed to fetch operator data");
        } else {
          setOperatorList(res?.data?.data?.Operator || []);
          setSelectedOperator(null);
          setSelectedCircle(null);
          setServicename(res?.data?.data?.service);
          if (res?.data?.data?.service?.service_name === "Mobile Recharge") {
            setIsMobileRecharge(true);
          }
          if (res?.data?.data?.service?.service_name === "DTH") {
            setIsDth(true);
          }

          // Mobile Recharge
        }
      } catch {
        setError("Something went wrong while fetching operators");
      } finally {
        setLoading(false);
      }
    };

    fetchOperators();
    handleReset();
  }, [serviceId]);

  // const handleInputChange = (paramName, value) => {
  //   setFormValues((prev) => ({ ...prev, [paramName]: value }));
  //   const param = selectedOperator?.customerParams?.find(p => p.name === paramName);
  //   if (param?.regex && !new RegExp(param.regex).test(value)) {
  //     setFormErrors((prev) => ({ ...prev, [paramName]: 'Invalid input' }));
  //   } else {
  //     setFormErrors((prev) => ({ ...prev, [paramName]: '' }));
  //   }
  // };

  const handleInputChange = (paramName, value) => {
    const param = selectedOperator?.customerParams?.find(
      (p) => p.name === paramName
    );

    // Skip validation and just set value if param is Amount
    if (param?.desc === "Amount") {
      setFormValues((prev) => ({ ...prev, [paramName]: value }));
      return;
    }

    // Otherwise, normal flow
    setFormValues((prev) => ({ ...prev, [paramName]: value }));

    if (param?.regex && !new RegExp(param.regex).test(value)) {
      setFormErrors((prev) => ({ ...prev, [paramName]: "Invalid input" }));
    } else {
      setFormErrors((prev) => ({ ...prev, [paramName]: "" }));
    }
  };

  useEffect(() => {
    if (isMobileRecharge) {
      fetchCircle();
    }
  }, [isMobileRecharge]);

  const fetchCircle = async () => {
    try {
      let res = await getMobileRechargeCircle();
      setCircle(res?.data?.data || []);
    } catch (error) {
      setCircle([]);
    }
  };

  const handleFetchMobilePlan = withLocation(async (latitude, longitude) => {
    if (isFetchingRef.current) return;
    setLoading(true);
    setError("");
    setplanDetails(null);
    setBillFetched(false);
    isFetchingRef.current = true;
    try {
      const body = {
        opretorId: selectedOperator?.operatorid,
        state: selectedCircle?.circle || "ALL INDIA",
        latitude,
        longitude,
      };

      try {
        let res = await getMobileRechargePlan(body);
        if (res?.data?.statusCode === 200) {
          setplanDetails(res?.data?.data);
          setBillFetched(false);
          setPlanPopupOpen(true);
        }
        toastMessage(res?.data?.error, res?.data?.message);
      } catch (error) {
        toastMessage(error?.data?.data?.error, error?.data?.data?.message);
      }
    } catch (error) {
    } finally {
      setLoading(false);
      isFetchingRef.current = false;
    }
  });

  const handleFetchBill = async () => {
    setLoading(true);
    setError("");
    setRefId("");
    setBillDetails(null);
    setBillFetched(false);

    try {
      const body = {
        billerId: selectedOperator?.operatorid,
        inputParameters: formValues,
      };
      if (!fetchRequired) {
        body.transactionAmount = billAmount;
      }

      const res = await getBBPSBill(body);
      const bill = res?.data?.data?.billerResponse;

      if (!bill) {
        toastMessage(res.data?.error, res?.data?.message);
      } else {
        setBillDetails(bill);
        setBillAmount(bill?.BillAmount || "");
        setBillFetched(true);
        setRefId(res?.data?.data?.refId);
      }
    } catch {
      setError("Failed to fetch bill. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleFetchBillBackground = async () => {
    setLoading(true);
    setError("");
    setRefId("");
    setBillDetails(null);
    setBillFetched(false);

    try {
      let cloneObj = { ...formValues };

      if (selectedOperator?.operatorid === "SUND00000NAT02") {
        cloneObj.param2 = billAmount;
      }

      const body = {
        billerId: selectedOperator?.operatorid,
        inputParameters: cloneObj,
      };
      if (!fetchRequired) {
        body.transactionAmount = billAmount;
      }

      const res = await getBBPSBill(body);
      const bill = res?.data?.data?.billerResponse;

      if (res?.data?.error === true) {
        toastMessage(res.data?.error, res?.data?.message);
      } else {
        setBillDetails(bill);
        // setBillAmount(bill?.BillAmount || '');
        setOpenTipModel(true);
        setBillFetched(false);
        setRefId(res?.data?.data?.refId);
      }
    } catch {
      setError("Failed to fetch bill. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  //Subimit after in tpin confirm button
  const handlePayBill = withLocation(async (latitude, longitude) => {
    // console.log('Paying bill of ₹', billAmount);
    setLoading(true);
    const billPaymentData = {
      billerId: selectedOperator?.operatorid,
      telecomCircle: "",
      inputParameters: formValues,
      latitude: String(latitude).slice(0, 7),
      longitude: String(longitude).slice(0, 7),

      amount: billAmount,
      enquiryReferenceId: refId,
      tpin: pin,
    };
    if (isMobileRecharge) {
      billPaymentData.telecomCircle = selectedCircle?.circleCode;
    }

    try {
      let res = await payBBPSBill(billPaymentData);
      if (res?.data?.error === false) {
        handleReset();
        walletCall();
        tipPopClose();
        navigate(`/bill-invoice/${res?.data?.data?.id}`);
      }
      setPin("");
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setLoading(false);
    }
  });

  const handleReset = () => {
    setFormValues({});
    setFormErrors({});
    setBillDetails(null);
    setBillAmount("");
    setBillFetched(false);
    setError("");
    setplanDetails(null);
    //    setSelectedCircle(null)
  };

  // const isFormValid = () => {
  //   return selectedOperator?.customerParams?.every(param => {
  //     const value = formValues[param.name] || '';
  //     const isValid = !param.regex || new RegExp(param.regex).test(value);
  //     return isValid && value.length >= param.minLength;
  //   });
  // };

  const isFormValid = () => {
    return selectedOperator?.customerParams?.every((param) => {
      if (param.desc === "Amount") return true; // Skip validation for hidden field

      const value = formValues[param.name] || "";
      const isValid = !param.regex || new RegExp(param.regex).test(value);
      return isValid && value.length >= param.minLength;
    });
  };
  const tipPopClose = () => {
    setOpenTipModel(false);
    setPin("");
  };

  const tab1 = planDetails?.plans ? Object.keys(planDetails?.plans) : [];
  const tab2 = planDetails?.offerPlan
    ? Object.keys(planDetails?.offerPlan)
    : [];
  const tabs = [...tab1, ...tab2];
  const Data1 = planDetails?.plans || {};
  const Data2 = planDetails?.offerPlan || {};
  const total = { ...Data1, ...Data2 };

  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = (_, data) => {
    setActiveTab(data);
  };
  const payMobileRecharge = (value) => {
    setBillAmount(value);
    setPlanPopupOpen(false);
    // setOpenTipModel(true)
  };
  // Dth and mobile pay handle

  const handlePay = async () => {
    if (!fetchRequired || isDth) {
      await handleFetchBillBackground();
    } else {
      setOpenTipModel(true);
    }
  };

  return (
    <>
      <Box
        sx={{
          background: "var(--bs-primary3)",
          color: "var(--bs-primary2)",
          backgroundSize: "cover",
          minHeight: "100vh",
          padding: 2,
        }}
      >
        <Box
          p={0.5}
          maxWidth={"620px"}
          m={"auto"}
          position="relative"
          zIndex={1}
        >
          {loading && <Loader />}
          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}

          <CustomBox>
            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
            >
              <Box display="flex" alignItems="center" gap={1}>
                {serviceName?.icon && (
                  <img
                    src={genrateUrl(serviceName?.icon)}
                    alt="Service Icon"
                    width={30}
                    height={30}
                    style={{ objectFit: "contain" }}
                    onError={(e) => {
                      e.currentTarget.src = "/bbpslogo.png";
                    }}
                  />
                )}
                <Typography variant="h6" gutterBottom mt={1} fontSize={18}>
                  {serviceName?.service_name}
                </Typography>
              </Box>

              {/* Right Side: BBPS Logo */}
              <Box>
                <img src="/bbpslogo.png" alt="BBPS Logo" width={90} />
              </Box>
            </Box>

            <Divider sx={{ mb: 2 }} />

            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Autocomplete
                  options={operatorList}
                  getOptionLabel={(option) => option.name}
                  value={selectedOperator}
                  disabled={billFetched}
                  onChange={(e, newVal) => {
                    setSelectedOperator(newVal);
                    let requiredFetch =
                      newVal?.fetchRequirement === "MANDATORY";
                    setFetchRequried(requiredFetch);
                    handleReset();
                  }}
                  fullWidth
                  renderOption={(props, option) => (
                    <li {...props} key={option._id}>
                      <Box
                        component="div"
                        sx={{
                          fontSize: "13px", // Change font size here
                          color: "var(--bs-primary2)", // Change text color here
                        }}
                        display={"flex"}
                        columnGap={1.5}
                        alignItems={"center"}
                      >
                        <img
                          src={option?.logo_url}
                          alt="Service Icon"
                          width={20}
                          height={20}
                          style={{ objectFit: "contain" }}
                          onError={(e) => {
                            e.currentTarget.src = "/bbpslogo.png";
                          }}
                        />
                        {option.name}
                      </Box>
                    </li>
                  )}
                  renderInput={(params) => (
                    <CustomTextField
                    className="mui-autofill-fix"
                      {...params}
                      label="Select Operator"
                      fullWidth
                      InputLabelProps={{
                        sx: {
                          "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                            top: "-6px",
                          },
                        },
                      }}
                    />
                  )}
                />
              </Grid>

              {/* //Mobile opertor on isMobileRecharge true  */}
              {selectedOperator && isMobileRecharge && (
                <Grid item xs={12} sm={6}>
                  <Autocomplete
                    options={circle}
                    getOptionLabel={(option) => option.circle}
                    value={selectedCircle}
                    onChange={(e, newVal) => {
                      setSelectedCircle(newVal);
                      // handleReset();
                    }}
                    disabled={billFetched}
                    fullWidth
                    renderOption={(props, option) => (
                      <li {...props}>
                        <Box
                          component="span"
                          sx={{
                            fontSize: "13px", // Change font size here
                            color: "var(--bs-primary2)", // Change text color here
                          }}
                        >
                          {option.circle}
                        </Box>
                      </li>
                    )}
                    renderInput={(params) => (
                      <CustomTextField
                      className="mui-autofill-fix"
                        {...params}
                        label="Select Circle"
                        fullWidth
                        InputLabelProps={{
                          sx: {
                            "&.MuiInputLabel-root:not(.MuiInputLabel-shrink)": {
                              top: "-6px",
                            },
                          },
                        }}
                      />
                    )}
                  />
                </Grid>
              )}

              {/* {selectedOperator?.customerParams?.map((param) => (
            <Grid item xs={12} sm={6} key={param.name}>
              <CustomTextField
                label={param.desc}
                
                type="text"
                value={formValues[param.name] || ''}
                inputProps={{
                  maxLength: param.maxLength,
                  minLength: param.minLength,
                  inputMode: param.inputType === "NUMERIC" ? "numeric" : "text",
                
                }}
                onChange={(e) => handleInputChange(param.name, e.target.value)}
                error={!!formErrors[param.name]}
                helperText={formErrors[param.name]}
                disabled={billFetched}
              />
            </Grid>
          ))} */}
              {selectedOperator?.customerParams?.map((param) => (
                <Grid
                  item
                  xs={12}
                  sm={6}
                  key={param.name}
                  sx={{ display: param.desc === "Amount" ? "none" : "block" }}
                >
                  <CustomTextField
                  className="mui-autofill-fix"
                    label={param.desc}
                    type={param.desc === "Amount" ? "hidden" : "text"}
                    value={formValues[param.name] || ""}
                    inputProps={{
                      maxLength: param.maxLength,
                      minLength: param.minLength,
                      inputMode:
                        param.inputType === "NUMERIC" ? "numeric" : "text",
                    }}
                    onChange={(e) =>
                      handleInputChange(param.name, e.target.value)
                    }
                    error={!!formErrors[param.name]}
                    helperText={formErrors[param.name]}
                    disabled={billFetched}
                  />
                </Grid>
              ))}

              {selectedOperator && !fetchRequired && (
                <>
                  <Grid item xs={12} sm={12}>
                    <Box sx={{ position: "relative" }}>
                      <CustomTextField
                      className="mui-autofill-fix"
                        label="Amount to Pay (₹)"
                        value={billAmount}
                        type="number"
                        disabled={isMobileRecharge}
                        onChange={(e) => setBillAmount(e.target.value)}
                      />
                      {((!fetchRequired && isDth) || isMobileRecharge) && (
                        <Box
                          sx={{
                            position: "absolute",
                            right: "10px",
                            top: "10px",
                            cursor: "pointer",
                            // backgroundColor: "var(--bs-primary3)",
                            color: "var(--bs-primary2)",

                            "&:hover": {
                              // backgroundColor: "var(--bs-primary1)",
                            },
                            "&.Mui-disabled": {
                              backgroundColor: "rgba(0,0,0,0.12)",
                              color: "var(--bs-primary2)",
                              opacity: 0.6,
                            },
                          }}
                          onClick={handleFetchMobilePlan}
                        >
                          <p style={{ fontSize: "12px", cursor: "pointer" }}>
                            View Plans
                          </p>
                        </Box>
                      )}
                    </Box>
                  </Grid>
                  <Grid container item sx={12} justifyContent={"center"}>
                    <Button
                      variant="contained"
                      disabled={
                        !selectedOperator || billAmount < 9 || !isFormValid()
                      }
                      onClick={handlePay}
                      sx={{
                        width: 250,
                        backgroundColor: "var(--bs-primary3)",
                        color: "var(--bs-primary2)",
                        textTransform: "none",
                        fontWeight: 600,
                        "&:hover": {
                          backgroundColor: "var(--bs-primary1)",
                        },
                        "&.Mui-disabled": {
                          backgroundColor: "rgba(0,0,0,0.12)",
                          color: "var(--bs-primary2)",
                          opacity: 0.6,
                        },
                      }}
                    >
                      Process to Pay {billAmount}
                    </Button>
                  </Grid>{" "}
                </>
              )}

              {selectedOperator && fetchRequired && (
                <Grid container item sx={12} justifyContent={"center"}>
                  <Button
                    variant="contained"
                    color="var(--bs-primary2)"
                    disabled={
                      !selectedOperator || !isFormValid() || billFetched
                    }
                    onClick={handleFetchBill}
                    sx={{
                      width: "250px",
                      borderColor: "var(--bs-primary3) !important", // Custom colors
                      backgroundColor: "#6C757D",
                      color: "#fff",
                    }}
                  >
                    Fetch Bill
                  </Button>
                </Grid>
              )}

              {/* {selectedOperator && !billFetched && (
            <Box display={'flex'} gap={2} mt={2}  sx={{
              flexDirection: {
                sm: 'column',
                md: 'row',
              },
              justifyContent:'center',
              width:"100%"
            }}
             >
              <Button
                variant="contained"
                color="primary"
                disabled={isMobileRecharge ? !isFormValid() || !selectedCircle : !isFormValid()}

                onClick={(isMobileRecharge || (isDth && fetchRequired) ) ? handleFetchMobilePlan :  handleFetchBill}

                sx={{
                       width:'250px',
                    borderColor: 'var(--bs-primary1)', // Custom colors
                    
                    backgroundColor: 'var(--bs-primary1)', // Custom colors
                    color: "white",
        
                  }}
              >
                {(isMobileRecharge || (isDth && fetchRequired) )  ? 'Fetch Plan' :"Fetch Bill" }
              </Button>

              {(!fetchRequired) &&
              <Button
                variant="contained"
                color="primary"
                disabled={ !isFormValid() || billAmount < 10}
                onClick={()=>payMobileRecharge(billAmount)}

                sx={{
                       width:'250px',
                    borderColor: 'var(--bs-primary1)', // Custom colors
                    marginLeft:2,
                    backgroundColor: 'var(--bs-primary1)', // Custom colors
                    color: "white",
        
                  }}
              >
                {'Pay Bill' }
              </Button> }
            </Box>
          )}

          {
            selectedOperator && billFetched && (
                <Box  display={'flex'} gap={2} mt={2}  sx={{
                  flexDirection: {
                    sm: 'column',
                    md: 'row',
                  },
                  justifyContent:'center',
                  width:"100%"
                }}>
                <Button
                variant='outlined'
                color="error"
                onClick={handleReset}
                sx={{
                    width:'250px'
                }}
              >
                Reset
              </Button>
              {( (!fetchRequired && !billFetched)   ) &&
              <Button
                variant="contained"
                color="primary"
                disabled={ !isFormValid() || billAmount < 10}
                onClick={()=>payMobileRecharge(billAmount)}

                sx={{
                       width:'250px',
                    borderColor: 'var(--bs-primary1)', // Custom colors
                  
                    backgroundColor: 'var(--bs-primary1)', // Custom colors
                    color: "white",
        
                  }}
              >
                {'Pay Bill' }
              </Button> }
              </Box>
            )
          } */}
            </Grid>
          </CustomBox>

          {billFetched && billDetails && (
            <CustomBox>
              <Typography variant="h6" gutterBottom>
                Bill Details
              </Typography>
              <Divider sx={{ mb: 2 }} />

              <Grid container spacing={2}>
                {[
                  ...(Array.isArray(billDetails?.CustomerParamsDetails)
                    ? billDetails.CustomerParamsDetails
                    : []),
                  ...(Array.isArray(billDetails?.AdditionalDetails)
                    ? billDetails.AdditionalDetails
                    : []),
                ]?.map((item, idx) => (
                  <Grid item xs={12} sm={6} key={idx}>
                    <Box
                      display="flex"
                      justifyContent="space-between"
                      border="1px solid #eee"
                      borderRadius={1}
                      p={1}
                      boxShadow={1}
                    >
                      <Typography variant="body2" color="var(--bs-primary2)">
                        {item.Name}
                      </Typography>
                      <Typography variant="body2" fontWeight={500}>
                        {item.Value}
                      </Typography>
                    </Box>
                  </Grid>
                ))}

                <Grid item xs={12} sm={6}>
                  <Box
                    display="flex"
                    justifyContent="space-between"
                    border="1px solid #eee"
                    borderRadius={1}
                    p={1}
                    boxShadow={1}
                  >
                    <Typography variant="body2" color="var(--bs-primary2)">
                      Customer Name
                    </Typography>
                    <Typography variant="body2" fontWeight={500}>
                      {billDetails.CustomerName}
                    </Typography>
                  </Box>
                </Grid>

                <Grid item xs={12} sm={6}>
                  <CustomTextField
                  className="mui-autofill-fix"
                    label="Amount to Pay (₹)"
                    value={billAmount}
                    type="number"
                    onChange={(e) => setBillAmount(e.target.value)}
                  />
                </Grid>

                <Grid container item xs={12} justifyContent="center">
                  <Button
                    variant="contained"
                    color="var(--bs-primary2)"
                    disabled={!billAmount}
                    // onClick={handlePayBill}
                    onClick={() => setOpenTipModel(true)}
                    // fullWidth

                    sx={{
                      backgroundColor: "var(--bs-primary1)",
                      borderColor: "var(--bs-primary1)",
                      width: "250px",
                    }}
                  >
                    Pay ₹{billAmount || ""}
                    {/* Pay */}
                  </Button>
                </Grid>
              </Grid>
            </CustomBox>
          )}
        </Box>
      </Box>

      <Dialog
        open={planPopupOpen}
        onClose={() => setPlanPopupOpen(false)}
        PaperProps={{
          sx: () => ({
            borderRadius: 4,
            p: 2,
            backdropFilter: "blur(10px)",
            background: "white",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 800,
            overflow: tabs?.length === 0 ? "hidden" : "auto",
          }),
        }}
      >
        <IconButton
          onClick={() => setPlanPopupOpen(false)}
          sx={{
            position: "absolute",
            top: 8,
            right: 8,
            zIndex: 10,
            color: "var(--bs-primary2)",
            fontSize: "16px",
          }}
        >
          <CloseIcon />
        </IconButton>
        <CustomBox sx={{ width: "100%", borderRadius: 2 }}>
          {/* Tab Buttons */}
          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            variant="scrollable"
            scrollButtons="auto"
            allowScrollButtonsMobile
            orientation="horizontal" // change to "vertical" if you want vertical tabs
            sx={{
              "& .MuiTabs-flexContainer": {
                gap: "8px",
              },
              "& .MuiTab-root": {
                fontSize: "14px",
                textTransform: "none",
                padding: "10px 16px",
                fontWeight: 500,
                color: "var(--bs-primary2)", // default tab color
              },
              "& .MuiTab-root.Mui-selected": {
                color: "var(--bs-primary2)", // selected tab color
                fontWeight: "bold",
              },
              "& .MuiTabs-indicator": {
                backgroundColor: "var(--bs-primary1)",
                height: "2px",
                borderRadius: "2px",
              },
            }}
          >
            {tabs?.map((tab, index) => (
              <Tab key={index} label={tab} />
            ))}
          </Tabs>
          {tabs?.length === 0 && (
            <Box
              width={"100%"}
              display={"flex"}
              flexDirection={"column"}
              justifyContent={"center"}
              alignItems={"center"}
            >
              <img src="/noplandata.png" style={{ top: "50%" }} width={100} />
              <Typography component={"h4"}>No Plan Found</Typography>
            </Box>
          )}

          {/* Tab Content */}
          <Box sx={{ mt: 2 }}>
            {/* Example content placeholder */}
            {/* Replace with your actual content based on activeTab */}
            <Typography variant="body1" color="var(--bs-primary2)">
              {/* Example: <PlanList data={totalData[activeKey] || []} selectAmount={selectAmount} /> */}
              {/* Content for: {total[tabs[activeTab]]} */}
              <Box sx={{ mt: 2 }}>
                {/* <RadioGroup value={selectedAmount?.toString() || ''} onChange={handleAmountChange}> */}
                <Grid
                  container
                  spacing={2}
                  height={"600px"}
                  sx={{ overflowY: "auto", overflowX: "hidden " }}
                >
                  {total[tabs[activeTab]]?.map((plan, idx) => (
                    <Grid item xs={12} key={idx}>
                      <Card
                        elevation={1}
                        onClick={() => payMobileRecharge(plan?.rs)}
                        sx={{
                          borderRadius: 3,
                          transition: "transform 0.3s",
                          "&:hover": { transform: "scale(1.02)" },
                          cursor: "pointer",
                        }}
                      >
                        <CardContent>
                          <Box
                            display="flex"
                            alignItems="center"
                            gap={1}
                            mb={1}
                          >
                            {/* <CurrencyRupeeIcon color="primary" /> */}
                            <Typography
                              variant="h5"
                              fontWeight="bold"
                              fontSize={16}
                            >
                              ₹{plan?.rs}
                            </Typography>
                          </Box>

                          <Divider sx={{ my: 1 }} />

                          <Box
                            display="flex"
                            alignItems="center"
                            gap={1}
                            mb={1}
                          >
                            {/* <AccessTimeIcon color="action" /> */}
                            <Typography
                              variant="body1"
                              color="var(--bs-primary2)"
                              fontSize={13}
                            >
                              Validity: {plan?.validity}
                            </Typography>
                          </Box>

                          <Box display="flex" alignItems="center" gap={1}>
                            {/* <InfoIcon color="info" /> */}
                            <Typography variant="body2" fontSize={13}>
                              {plan?.desc}
                            </Typography>
                          </Box>
                        </CardContent>
                      </Card>
                    </Grid>
                  ))}
                </Grid>
                {/* </RadioGroup> */}
              </Box>
            </Typography>
          </Box>
        </CustomBox>

        {/* <DialogActions sx={{ justifyContent: 'center', mt: 2 }}>
    <Button onClose={()=> setPlanPopupOpen(false)} color="error" variant="outlined" sx={{ borderRadius: 2,
        px: 4,width:'150px'}}>
      Cancel
    </Button>
    </DialogActions> */}
      </Dialog>

      <Dialog
        open={openTipModel}
        onClose={tipPopClose}
        PaperProps={{
          sx: {
            borderRadius: 4,
            p: 2,
            backdropFilter: "blur(10px)",
            background: "rgba(255, 255, 255, 0.9)",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 400,
          },
        }}
      >
        <DialogTitle
        className="mui-autofill-fix"
          sx={{
            color: "var(--bs-primary2)",
            fontWeight: 600,
            textAlign: "center",
            fontSize: "1.0rem",
          }}
        >
          Enter TPIN
        </DialogTitle>

        <DialogContent sx={{ textAlign: "center" }}>
          <Typography className="mui-autofill-fix" sx={{ color: "var(--bs-primary2)", mb: 0.5 }} fontSize={14}>
            Please enter your 4-digit TPIN to continue.
          </Typography>

          <Box
            display="flex"
            justifyContent="center"
            alignItems="center"
            mb={2}
            gap={1}
          >
            {/* <CurrencyRupeeIcon color="success" /> */}
            <Typography
            className="mui-autofill-fix"
              variant="h6"
              sx={{ color: "var(--bs-primary2)", mb: 0.5 }}
              fontSize={14}
            >
              Payable Amount: ₹{billAmount}
            </Typography>
          </Box>

          <CustomTextField
          className="mui-autofill-fix"
            label="TPIN"
            type="password"
            value={pin || ""}
            onChange={(e) => {
              const value = e.target.value;
              if (/^\d{0,4}$/.test(value)) {
                setPin(value);
              }
            }}
            autoComplete="off"
            inputProps={{ inputMode: "numeric", maxLength: 4 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <LockIcon />
                </InputAdornment>
              ),
            }}
            // sx={{ mt: 2 }}
            fullWidth
          />
        </DialogContent>

        <DialogActions sx={{ justifyContent: "center", mt: 2 }}>
          <Button
            onClick={tipPopClose}
            color="var(--bs-primary2)"
            variant="outlined"
            sx={{ borderRadius: 2, px: 4, width: "150px" }}
          >
            Cancel
          </Button>
          <Button
            onClick={() => {
              if (pin?.length < 4) {
                alert("Please enter 4 digit TPIN");
                return;
              }
              handlePayBill();
            }}
            disabled={loading}
            color="var(--bs-primary2)"
            variant="contained"
            sx={{
              backgroundColor: "var(--bs-primary1)",
              borderRadius: 2,
              px: 4,
              width: "150px",
            }}
          >
            Submit
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default BBPSServices;
